import React from "react";
import { connect } from "react-redux";
import "./style.css";

class Result extends React.Component {
  state = { clicked: 0 };

  fillPhoto = () => {
    try {
      return (
        <img
          alt={
            this.props.searchresult.state.items[this.state.clicked].pagemap
              .cse_thumbnail[0].src
          }
          src={
            this.props.searchresult.state.items[this.state.clicked].pagemap
              .cse_thumbnail[0].src
          }
        />
      );
    } catch (err) {}
  };

  fillResults = () => {
    try {
      console.log(this.props.searchresult.state.items[this.state.clicked]);
      return (
        <div>
          <h5>
            {this.props.searchresult.state.items[this.state.clicked].title}
          </h5>

          <h6>
            {this.props.searchresult.state.items[this.state.clicked].snippet}
          </h6>

          {this.fillPhoto()}
        </div>
      );
    } catch (err) {
      return "";
    }
  };

  next = () => {
    try {
      if (this.state.clicked + 1 < this.props.searchresult.state.items.length) {
        this.setState({ clicked: this.state.clicked + 1 });
      }
    } catch (err) {}
  };

  prev = () => {
    try {
      if (this.state.clicked - 1 >= 0) {
        this.setState({ clicked: this.state.clicked - 1 });
      }
    } catch (err) {}
  };

  render() {
    return (
      <div>
        <div id="buttons">
          <button onClick={this.prev}>poprzedni</button>
          <button onClick={this.next}>nastÄ™pny</button>
        </div>
        {this.fillResults()}
      </div>
    );
  }
}
const mapStateToProps = state => {
  return {
    searchresult: state.searchresult
  };
};

export default connect(
  mapStateToProps,
  {}
)(Result);
